import "./index.scss";
import { axios } from "&";
import { Head } from "~/components/head";
import { List } from "antd-mobile";
import { connect } from "react-redux";
import { history } from "&/history";
const Item = List.Item;
const Brief = Item.Brief;
@connect(state => {
  return {
    touxiang: state.getIn(["data", "touxiang"])
  };
})
export default class Fit extends Component {
  constructor() {
    super();
    this.state = {
      pic: ""
    };
  }
  componentDidMount() {
    if (!sessionStorage.token) {
      history.push("/login");
    }
    axios.post("/react/getAvatar").then(res => {
      if (!!res.data.type) {
        this.setState({
          pic: res.data.result.pic.replace(/public/, "https://peng47.com:1907/")
        });
      } else {
        this.setState({
          pic: require("@/assets/images/touxiang.jpg")
        });
      }
    });
  }
  clearLogin = () => {
    sessionStorage.token = "";
    sessionStorage.mobile = "";
    history.push("/login");
  };
  render() {
    return (
      <div id="fit">
        <Head title="设置" leftShow={true}></Head>
        <List renderHeader={() => ""} className="my-list">
          <Item
            arrow="horizontal"
            thumb={this.state.pic}
            multipleLine
            onClick={() => {}}
          >
            mobile <Brief>暂无签名</Brief>
          </Item>
        </List>
        <List renderHeader={() => ""} className="my-list">
          <Item
            arrow="horizontal"
            multipleLine
            onClick={() => {}}
            extra={"暂未开通会员"}
          >
            会员中心
          </Item>
        </List>

        <List renderHeader={() => ""} className="my-list">
          <Item
            arrow="horizontal"
            multipleLine
            onClick={() => {}}
            extra={"yiyan-app"}
          >
            一言微信公众号
          </Item>
          <Item
            arrow="horizontal"
            multipleLine
            onClick={() => {}}
            extra={"一言APP"}
          >
            一言微博号
          </Item>
        </List>
        <List renderHeader={() => ""} className="my-list">
          <Item arrow="horizontal" onClick={() => {}}>
            关于一言
          </Item>
        </List>
        <List renderHeader={() => ""} className="my-list">
          <Item arrow="horizontal" onClick={() => {}}>
            字体大小
          </Item>
          <Item arrow="horizontal" onClick={() => {}}>
            屏蔽管理
          </Item>
        </List>
        <List renderHeader={() => ""} className="my-list">
          <Item arrow="horizontal" onClick={() => {}}>
            给[一言]的建议
          </Item>
        </List>
        <List renderHeader={() => ""} className="my-list">
          <Item arrow="horizontal" onClick={() => {}}>
            清除缓存
          </Item>
          <Item arrow="horizontal" onClick={this.clearLogin}>
            退出登录
          </Item>
        </List>
        <p
          style={{
            textAlign: "center",
            color: "#ccc",
            fontFamily: "仿宋",
            fontSize: "0.4rem",
            margin: "0.2rem 0"
          }}
        >
          一言3.31
        </p>
      </div>
    );
  }
}
